.investment {
    display: flex;
    background: #fbfbfb;
    border-top-right-radius: 3rem;
    align-items: center;
    padding: 2rem 3rem;
    @include media-breakpoint-down(sm) {
        flex-direction: column;   
    }
    &-avatar {
        flex: 0 0  72px;
        margin-right: 2rem;
        @include media-breakpoint-down(sm) {
            margin-right: 0;
        }   
    }
    &-text {
        flex: 1 1 auto;
    }
    &-price {
        flex: 0 1 auto;
        font-weight: bold;
        color: $primary;

    }
}