.account-menu {
    .nav {
        padding-top: 1rem;
        padding-bottom: 1rem;
        .nav-item {
            .nav-link {
                padding-bottom: 1rem;
                padding-top: 1rem;
                color: #000;
                text-decoration: none;
                text-transform: uppercase;
                font-weight: bold;
                &.active {
                    color: $primary;
                }
            }
        }
    }
}