.account-table {
    font-size: 14px;
    margin-top: 2rem;
    margin-bottom: 2rem;
    tr {
        th, td {
            text-align: center;
        }
        th {
            font-weight: 600;
        }
    }
    a {
        color: #000;
        text-decoration: none;
        font-weight: 500;
        svg.icon {
            width: 27px;
            height: 25px;
        }
    }
}