
.form-control, .form-select {
    border: 0;
    color: #000;
    border-bottom: 1px solid #000;
    padding: 0.75rem 1rem;
    font-weight: 500;
    font-size: 14px;
    &::placeholder {
        text-transform: uppercase;
        color: #000;
        font-weight: 500;
        font-size: 14px;

    }
}

.form-light {
    .form-control {
        border-bottom: 1px solid #f6f6f8;
    }
}

.input-sensitive {
    position: relative;
    .password-toggle {
        position: absolute;
        top: 50%;
        right: 1rem;
        width: 16px;
        height: 10px;
        transform: translateY(-50%);
        display: flex;
        align-items: center;
        justify-content: center;
        color: #4c4c4c;
        &:after {
            position: absolute;
            top: 50%;
            left:50%;
            transform: translate(-50%, -50%) rotate(30deg);
            content: '';
            width: 2px;
            border-right: 2px solid #fff;
            height: 100%;
            background: #000;
            opacity: 0;

        }
        svg {
            display: block;
            width: 16px;
            height: 10px;
        }
        &.enabled {
            &:after {
                opacity: 1;
            }
        }
    }
}