.advantages {
    display: flex;
    flex-wrap:wrap;
    align-items: center;

    p {
        margin-bottom: 0;
    }
    h3 {
        color: $primary;
        font-weight: 700;
        font-size: 21px;
    }
    
    &-item {
        flex: 1 0 100%;
        @include media-breakpoint-up(md) {
            flex: 1 0 50%;
        }
        @include media-breakpoint-up(lg) {
            flex: 1 1 auto;
        }
        position: relative;
        padding-left: 2rem;
        padding-right: 2rem;
        text-align: center;
        margin-top: 1rem;
        margin-bottom: 1rem;
        + .advantages-item {
            @include media-breakpoint-up(lg) {

                &:before {
                    position: absolute;
                    top: 50%;
                    left: 0;
                    height: 70px;
                    width: 1px;
                    background: #cbcbcb;
                    margin-top: -35px;
                    content: '';

                }
            }
        }
    }
    &-icon {
        margin-bottom: 1rem;
        img {
            height: 57px;
        }
    }
}