.navbar {
    .navbar-line1 {
        padding-top: 2rem;
        padding-bottom: 3rem;
        align-items: center;
        
        @include media-breakpoint-down(lg) {
            padding-top: 1rem;
            padding-bottom: 1rem;
        }
        @include media-breakpoint-down(sm) {
            padding-top: 0;
            padding-bottom: 0;
            justify-content: center;
            text-align: center;
        }
    }

    .nav-social {
        margin-left: 1rem;
        @include media-breakpoint-down(md) {
            flex: 0 0 auto;
        }
        .nav-item {
            .nav-link {
                padding-left: 0.5rem;
                padding-right: 0.5rem;
            }
        }
        svg.icon {
            width: 22px;
            height: 22px;
            @include transition($transition-base);
        }
    }
    .nav-language {
        position: relative;
        &:before, &:after {
            position: absolute;
            top: 50%;
            transform:translateY(-40%);
            width: 1px;
            height: 16px;
            content: '';
            background: #000;
        }
        &:before {

            left: 0;
            @include media-breakpoint-down(sm) {
                display: none;
            }
        }
        &:after {
            right: 0;

        }
        .nav-item {
            .nav-link {
                color: #000;
                @include media-breakpoint-down(sm) {
                    padding-top: .1rem;
                    padding-bottom: 0.1rem;
                }

            }
        }
    }
    .nav-links {
        @include media-breakpoint-down(sm) {
            justify-content: center;
            margin-top: 1rem;
        }
        @include media-breakpoint-down(md) {
            flex: 0 0 auto;
        }
        .nav-item {
            .nav-link {
                color: #000;
                text-transform: uppercase;
                padding-left: 1rem;
                padding-right: 1rem;
                font-family: 'Museo Sans';
                @include media-breakpoint-down(sm) {
                    padding-top: .1rem;
                    padding-bottom: 0.1rem;
                }

                &:hover {
                    color: $primary;
                }
            }
            + .nav-item {
                position: relative;
                &:before {
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform:translateY(-40%);
                    width: 1px;
                    height: 16px;
                    content: '';
                    background: #000;
                }
            }
        }

    }

    .navbar-toggler {
        border: 0;
    }

    .navbar-brand {
        // max-width:200px;
        display: block;
        
        @media(max-width:1500px) and (min-width:1200px) {
            // max-width:200px;
        }
        @media(max-width:420px) {
            // max-width:50%;
        }
        img {
            max-width:100%;
            height: auto;
        }
    }
    .navbar-nav {
        display: flex;
        width: 100%;        
        gap:15px;
        .nav-item {
            flex-grow: 1;
            flex-shrink: 0;
            flex-basis:content;
            

            a {
                @include media-breakpoint-up(lg) {
                background: $primary;
                color: #fff;
                }
                font-weight: 500;
                @media(max-width:1500px) and (min-width:1200px) {
                    font-size: 14px;
                }
            }
            .nav-link {
                padding: .8rem 2rem;
                border-radius: 2rem;
                text-align: center;
                text-transform: uppercase;
                @include media-breakpoint-up(lg) {
                    color: #fff;
                }
                &:hover, &.active {
                    @include media-breakpoint-up(lg) {
                        background: $success;
                    }
                }
            }
        }
    }
    .dropdown-toggle {
        &:after {
            display: none;
        }
        .icon {
            height: 9px;
            width: 6px;
            transform:rotate(90deg);
            @include transition($transition-base);
        }
        &:hover, &.show {
            .icon {
            transform:rotate(-90deg);
                
            }
        }
    }
    .dropdown-menu {
        border: 0;
        background: #e7e6e6;
        padding-left: 1rem;
        padding-right: 1rem;
        .dropdown-item {
            font-weight: bold;
            &:hover, &.active {
                color: $primary;
                background: transparent;
            }
        }
    }
}