.invest {
    h4 {
        font-weight: bold;
    }
}

.invest-summary {
    // background: #c6e8cc;
    position: relative;
    padding-top: 2rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 3rem;
        padding-bottom: 3rem;
    }


    
    &:before {
        position: absolute;
        top: 0;
        left: 0;
        content: '';
        background: #c6e8cc;
        width: 100%;
        height: 100%;
        border-top-right-radius: 100px;
        z-index: 10;
        @each $breakpoint, $container-max-width in $container-max-widths {
            @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
              width: calc(100% + ((100vw - #{$container-max-width}) / 2) + 30px);
              margin-left: calc(0px - (100vw - #{$container-max-width}) / 2 - 15px);
            }
          }
    }

    &-content {
        position: relative;
        z-index: 50;
    }
    p {
        margin-bottom: .5rem;
    }

}



.form-payments {
    display: flex;
    align-items: center;
    flex-wrap:wrap;

    .form-payments-item {
        flex: 0 0 auto;
        min-width:180px;
        + .form-payments-item {
            margin-left: 1rem;
        }
        .form-check {
            width: 100%;

            display: flex;
            align-items: center;
            padding-left: 2rem;
            &-label {
                flex: 0 0 auto;
                width: 100%;
            }
        } 

    }
}