.contact-info {
    font-size: 21px;
    strong {
        font-size: 24px;
        font-weight: bold;
    }
}

.contact-iod {
    margin-top: 2.5rem;
    strong {
        font-size: 18px;
    }
    p {
        margin-bottom: 1rem;
    }
}

.contact-details {
    display: flex;
    align-items: center;
    font-size: 21px;
    
    &-icon {
        flex: 0 0 auto;
        margin-right: 2rem;
        position: relative;
        color: $primary;
        font-size: 23px;
        font-weight: 700;
        text-align: center;
        
    }
    &-text {
        flex: 1 1 auto;

    }
    .icon {
        position: relative;
        z-index: 50;
        
           width: 18px;
           height: 18px;

    }
}

#googlemap {
    margin-top: 3rem;
    margin-bottom: 3rem;
    border-top-right-radius: 120px;
    overflow: hidden;
}

.contact-form {
    h3 {
        font-family: 'Museo Sans';
        text-transform: uppercase;
        font-weight: 300;
        margin-bottom: 2.5rem;
        margin-top: 2.5rem;
        strong {
            font-weight: 500;
            color: $primary;
        }
    }
    .form-control {
        border-color: #f5f5f8;
        &::placeholder {
            color: #737373;
        }
    }
}