.campaigns {
    padding-top: 2rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 4rem;
        padding-bottom: 2rem;
    }
}

.filters {
    margin-bottom: 2rem;
    font-family: 'Museo Sans';
    @include media-breakpoint-up(lg) {
        margin-bottom: 4rem;
    }
    display: flex;
    &-label {
        text-transform: uppercase;
        margin-right: 1rem;
        font-weight: 100;
    }
    &-items {
        display: flex;
        flex-wrap: wrap;
        &-item {
            font-weight: 500;
            text-transform: uppercase;
            padding-left: 1rem;
            padding-right: 1rem;
            color: #000;
            &.active {
                color: $green;
            }
        }
    }

}

.campaign-buttons {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
    // align-items: center;

    .btn {
        border-radius: 4px;
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        &:hover {
            color: #fff;
        }
        + .btn {
            margin-left: 0.5rem;
        }
    }
}

.single-campaign {

    .campaign-info {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .campaign-actions {
        margin-top: auto;
    }
    .campaign-progress {
        p.label {
            margin-bottom: 0;
        }
        p.amount {
            font-size: 24px;
        }
        .amount--current {
            font-weight: 700;
        }
        .amount--target {
            font-weight: 400;
        }
    }

    .campaign-countdown {
        margin-top: 2rem;
        margin-bottom: 2rem;
    }

    .nav-social {
        .nav-item {
            .nav-link {
                padding-left: 0.5rem;
                padding-right: 0.5rem;
                color: $success;
                svg.icon {
                    width: 24px;
                    height: 24px;
                    opacity: 0.7;
                    @include transition($transition-base);
                }
                &:hover {
                    svg.icon {
                        opacity: 1;
                    }
                }
            }
        }
    }

}
.related-campaigns-section {
    border-top: 1px solid #cfcfce;

}
.related-campaigns-section, .upcoming-campaigns-section, .finished-campaigns-section {
    margin-top: 3rem;
    padding-top: 3rem;
    position: relative;
    .container {
        position: relative;
    }
    .campaigns-readmore {
        .btn {
            font-weight: 400;   
        }
        @include media-breakpoint-up(lg) {
            position: absolute;
            bottom: 0;
            right: 0;
        }
    }
}


.nav-tabs {

    .nav-item {
        flex: 1 0 auto;
        .nav-link {
            width: 100%;
            font-weight: bold;
            color: #000;
            position: relative;
            &:after {
                position: absolute;
                bottom: -2px;
                left: 0;
                width: 100%;
                height: 5px;
                content: '';
                background: $success;
                border-radius: 2px;
                opacity: 0;
                @include transition($transition-base);
            }

            &.active {
                &:after {
                    opacity: 1;
                }
                border: 0;
                color: $success;
            }
        }
    }
}