.cta {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    width: 100%;
    padding-top: 2rem;
    padding-bottom: 2rem;
    &--style1 {
        text-align: right;
        .cta-large {
            display: block;
            text-transform: uppercase;
            font-family: 'Museo Sans';
            font-weight: 500;
            color: $primary;
            font-size: 48px;
            line-height: 1.2;
            margin-bottom: .5rem;
        }
        .cta-small {
            display: block;
            font-family: 'Museo Sans';
            font-weight: 100;
            font-size: 36px;
            color: #000;
        }
        svg {
            margin-left: auto;
            margin-top: 2rem;
        }
    }
}