.card-main-image {
    position: relative;
    background: transparent;
    &-flags {
        position: absolute;
        top: 0;
        left: 0;
        padding: 8px 24px;
        color: #fff;
        background: rgba($success,.9);
        font-size: 12px;
        z-index: 80;

    }
    &-like {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 38px;
        height: 38px;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 80;
        background: $primary;
        color: #fff;
        svg {
            width: 21px;
            height: 19px;
        }
    }
    &-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        content: '';
        background: rgba($primary,.8);
        display: flex;
        align-items: center;
        justify-content: center;
        @include transition($transition-base);
        z-index: 50;
        opacity: 0;
        color: #fff;
        text-transform: uppercase;
        font-weight: 500;
    }
}

.card {
    background: transparent;
    border: 0;
    &:hover {
        .card-main-image {
            &-overlay {
                opacity: 1;
            }
        }
        .card-body {
            box-shadow: 0 0 32px rgba(0,0,0,0.20);
        }
    }
    .card-body {
        padding: 2rem 2rem;
        @include transition($transition-base);
    }
    .card-date {
        margin-bottom: 1.5rem;
        // font-weight: bold;
        font-weight: 600;
        font-size: 14px;
    }
    .card-readmore {
        text-align: left;
        text-transform: uppercase;
        width: 100%;
        margin-top: 1.5rem;
        padding-top: 1rem;
        padding-bottom: 1rem;
        display: block;
        position: relative;
        &:after {
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(0, -50%);
            width: 60px;
            height: 7px;
            content: '';
            background-image: url('data:image/svg+xml,<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 392.4 51.1" style="enable-background:new 0 0 392.4 51.1;" xml:space="preserve"><path fill="%23000" d="M346.8,8.5L366,21.6l-366,0v10.2l363.7,0l-18.1,10.4l5.1,8.9l41.7-24L352.5,0L346.8,8.5z"/></svg>');
            background-repeat: no-repeat;
            background-position: center center;
        }

        
    }
    .card-title { 
        margin-bottom: 1.5rem;
        font-weight: bold;
        text-transform: uppercase;
    }

    .card-progress {
        margin-top: 2rem;
        margin-bottom: 2rem;
    }
    .card-intro {
        padding-bottom: 1rem;
        border-bottom: 1px solid #494746;
        margin-bottom: 1rem;
        font-weight: bold;
    }

}

.card-dark {
    .card-img-top {
        border: 0;
    }
    .card-body {
        border: 0;
        background: #252322;
        @include transition($transition-base);
        color: #fff;
    }
    &:hover {
        .card-body {
            background: #363433;
        }
    }
}

.card-light {
    .card-body {
        background: #fbfbfb;
        color: #000;
    }
}


.card-campaign {
    .card-body {
        border: 1px solid #e7e7e7;
        background: #fbfbfb;
    }
}

.card-news {
    .card-title {
        margin-bottom: 1rem;
    }
    .card-body {
        background: #fbfbfb;
    }
}

.news-decoration {
    @include media-breakpoint-up(lg) {
        transform: translateY(-20%);
        margin-bottom: -20%;
    }
}