.account-bar {
    position: relative;
    margin-bottom: 2rem;
    &:before {
        position: absolute;
        bottom: -0.75rem;
        left: 0;
        content: '';
        background: #c6e8cc;
        width: 100%;
        height: calc(100% - 1rem);
        border-top-right-radius: 40px;
        z-index: 10;
        @each $breakpoint, $container-max-width in $container-max-widths {
            @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
              width: calc(100% + ((100vw - #{$container-max-width}) / 2) + 30px);
              margin-left: calc(0px - (100vw - #{$container-max-width}) / 2 - 15px);
            }
          }
    }
    &-content {
        position: relative;
        z-index: 50;
        display: flex;
        flex-wrap:wrap;
        align-items: flex-end;
    }
    &-avatar {
        flex: 0 0 96px;
        margin-right: 2rem;
    }
    &-text {
        flex: 1 1 auto;
        .name {
            font-weight: bold;
            font-size: 21px;
        }
        .experience {
            font-size: 14px;
        }
    }
    &-menu-toggle {
        @include media-breakpoint-down(md) {
            flex:1 1 100%;
            text-align: center;
            padding-top: 0.5rem;
        background: #c6e8cc;
    }
    }
}