.page-header {
    padding-top: 0rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }

    h1 {
        text-transform: uppercase;
        font-weight: 100;
        font-family: 'Museo Sans';
    }

    .breadcrumbs {
        a {
            color: #000;
        }
        svg.icon {
            width: 15px;
            height: 16px;
            color: #000;
        }

    }
}

.text-page-intro {
    .page-header {
        .container {
            padding: 0;
        }
    }
}