.files {
    display: flex;
    flex-wrap:wrap;
    .file-download {
        flex: 0 0 100%;
        @include media-breakpoint-up(lg) {
            flex: 0 0 50%;
        }
        display: flex;
        align-items: center;
        padding: 1.5rem 3rem;
        margin-bottom: 1rem;
        @include transition($transition-base);
        &:hover {
            box-shadow: 0 6px 7px rgba(0,0,0,0.09);
        }
        .file-icon {
            flex: 0 0 40px;
            margin-right: 3rem;
        }
        .file-title {
            flex: 1 1 auto;
            color: #000;

            &-main {
                margin-bottom: 0.5rem;
                display: block;
            }
            &-additional {
                color: #515151;
                font-size: 14px;
            }
        }
    }
}