.home-about {
    padding-top: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 5rem;
    }
    p.lead {
        text-transform: uppercase;
        font-weight: 500;
    }
    p {
        margin-bottom: 2rem;
    }
}



.cta-invest {
    margin-top: 4rem;
    margin-bottom: 4rem;
    &-image {
        @include media-breakpoint-up(lg) {
            position: relative;
            left: -10%;
        }
    }
    .bg-green-left {
        @include media-breakpoint-up(lg) {
            padding: 0;
        }
        &:before {
            @include media-breakpoint-up(lg) {
                height: calc(100% - 7rem);
                top: 3.5rem;
            }
            
        }

    }
    img {
        margin-bottom: 2rem;
        @include media-breakpoint-up(md) {
            margin-bottom: 0;
        }
    }

    h2 {
        font-family: 'Museo Sans';
        

        font-size: 30px;
        @include media-breakpoint-up(lg) {

            font-size: 40px;
        }
        text-transform: uppercase;
        margin-bottom: 2rem;
    }
}

.home-news {
    margin-top: 4rem;
    margin-bottom: 3rem;
}

.upcoming-campaigns-section {
    position: relative;
    .upcoming-campaigns-decoration {
        position: absolute;
        right: 0;
        top: 0;
        width: 20%;
        z-index: 40;
        transform: translateY(-30%);
    }
    .bg-light-right {
        &:before {
            @each $breakpoint, $container-max-width in $container-max-widths {
                @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
                  width: calc(100% + ((100vw - #{$container-max-width}) / 2) + ((100vw - #{$container-max-width}) / 4) + 15px);
                  margin-right: calc(0px - (100vw - #{$container-max-width}) / 2 - 15px);
                  margin-left: calc(0px - (100vw - #{$container-max-width}) / 4 - 15px);
                }
              }
        }
    }
}