.account-filters {
    margin-top: 3rem;
    margin-bottom: 1rem;
    .form-label {
        font-family: 'Museo Sans';
        font-weight: 500;
        font-size: 14px;
    }
    .form-control {
        border: 1px solid #a4a4a4;
        border-radius: 25px;
    }
    .form-select {
        border: 1px solid #a4a4a4;
        border-radius: 25px;
    }
    .field-has-label {
        position: relative;
        label {
            font-size: 12px;
            display: inline-block;
            padding-left: 0.5rem;
            padding-right: 0.5rem;
            background: $body-bg;
            position: absolute;
            top: .1rem;
            left: 1rem;
            color: #000;
            transform: translateY(-50%);
        }
    }
}