.site-footer {
    margin-top: 4rem;
    background: $primary;
    color: #fff;
    padding-top: 2rem;
    padding-bottom: 2rem;
    position: relative;
    overflow: hidden;
    @include media-breakpoint-down(lg) {
        text-align: center;
    }
    .footer-logo {
        margin-bottom: 3rem;
    }

    a {
        color: #fff;
    }
    .footer-nav {
        .nav-item {
            flex: 0 0 100%;
            font-weight: bold;
            @include media-breakpoint-up(lg) {
                flex: 0 0 33%;
            }
            .nav-link {
                font-weight: 500;
                padding-top: 0.5rem;
                padding-bottom: 0.5rem;
                @include media-breakpoint-up(lg) {
                    padding-left: 0;
                }
                text-transform: uppercase;
                @include transition($transition-base);
                &:hover {
                    padding-left: 20px;
                    // color: $primary;
                    color: #efefef;

                }
            }
        }
    }

    .site-info {
        margin-top: 2rem;
        padding-top: 1rem;
        border-top: 1px solid rgba(255,255,255,.4);
    }

    .nav-social {

        @include media-breakpoint-down(lg) {
            justify-content: center;
        }
        svg.icon {
            width: 22px;
            height: 22px;
            @include transition($transition-base);
        }
        .nav-item {
            .nav-link {
                color: #fff;
                &:hover {
                    color: #fff;
                    .icon {
                        -webkit-filter: drop-shadow(0px 0px 10px rgba(255,0,0,1));      
                        filter: drop-shadow(0px 0px 10px rgba(255,0,0,1)); 
                    }
                }
            }
        }
    }
}