    // --#{$prefix}accordion-inner-border-radius: #{$accordion-inner-border-radius};
    // --#{$prefix}accordion-btn-padding-x: #{$accordion-button-padding-x};
    // --#{$prefix}accordion-btn-padding-y: #{$accordion-button-padding-y * 2};
    // --#{$prefix}accordion-btn-color: #{$accordion-color};
    // --#{$prefix}accordion-btn-bg: #{$accordion-bg};
    // --#{$prefix}accordion-btn-icon: #{escape-svg($accordion-button-icon)};
    // --#{$prefix}accordion-btn-icon-width: #{$accordion-icon-width};
    // --#{$prefix}accordion-btn-icon-transform: #{$accordion-icon-transform};
    // --#{$prefix}accordion-btn-icon-transition: #{$accordion-icon-transition};
    // --#{$prefix}accordion-btn-active-icon: #{escape-svg($accordion-button-active-icon)};
    // --#{$prefix}accordion-btn-focus-border-color: transparent;
    // --#{$prefix}accordion-btn-focus-box-shadow: 0 0 0;
    // --#{$prefix}accordion-body-padding-x: #{$accordion-body-padding-x};
    // --#{$prefix}accordion-body-padding-y: #{$accordion-body-padding-y};
    // --#{$prefix}accordion-active-color: #{$primary};
    --#{$prefix}accordion-active-bg: #{$accordion-bg};


    .accordion-button:not(.collapsed) {
        box-shadow: 0 0 0;
    }
    .accordion-button {
        font-weight: bold;
        border: 0;
        border-top: 2px solid #f2f2f2;
        border-top-right-radius: 44px;
        padding-right: 2rem;
        padding-left: 2rem;

        

    }

    .accordion-body {
        padding-left: 2rem;
        padding-right: 4rem;
    }
    .accordion-button::after {
        background-image: escape-svg(url("data:image/svg+xml,<svg version='1.1' x='0px' y='0px' viewBox='0 0 51.099999 230.4' xml:space='preserve' width='51.099998' height='230.39999' xmlns='http://www.w3.org/2000/svg' xmlns:svg='http://www.w3.org/2000/svg'> <path d='M 42.6,184.8 29.5,204 V 0 H 19.3 V 201.7 L 8.9,183.6 0,188.7 24,230.4 51.1,190.5 Z' id='path2' /> </svg>  "));
        background-size: contain;
        background-position: center center;
    }
    .accordion-item:first-of-type .accordion-button {
        border-top-right-radius: 44px;
    }
    .accordion-item {
        margin-bottom: 0.5rem;
        border: 2px solid #f2f2f2;
        border-top: 0;
        border-top-right-radius: 44px;

        
    }


    &--solid {
    .accordion-item {
        border: 0;
        background: #fbfbfb;
    }
        .accordion-button {
            border: 0;
            background: #fbfbfb;
        }

    }
}