.hidden {
    display: none;
}

.page {
    overflow: hidden;
}

.btn {
    padding: 15px 30px;
    font-weight: bold;
    text-transform: uppercase;
}
a {
    @include transition($transition-base);
}

.font-museo {
    font-family: 'Museo Sans';
}

.escape-container-left {
	@each $breakpoint, $container-max-width in $container-max-widths {
		@include media-breakpoint-up($breakpoint, $grid-breakpoints) {
		  width: calc(100% + ((100vw - #{$container-max-width}) / 2) + 30px);
		  margin-left: calc(0px - (100vw - #{$container-max-width}) / 2 - 15px);
		}
	  }
}

.escape-container-right {
	@each $breakpoint, $container-max-width in $container-max-widths {
		@include media-breakpoint-up($breakpoint, $grid-breakpoints) {
		  width: calc(100% + ((100vw - #{$container-max-width}) / 2) + 30px);
		  margin-right: calc(0px - (100vw - #{$container-max-width}) / 2 - 15px);
		}
	  }
}


.btn-primary {
    font-weight: 400;
    text-transform: uppercase;
    background: #caddff;
    border-color: #caddff;
    color: #000;
    border-radius: 30px;
    border-top-right-radius: 0;
    position: relative;
    padding-left: 35px;
    padding-right: 50px;
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-right: 2rem;
    &:after {
        position: absolute;
        top: 50%;
        right: 0;
        width: 60px;
        height: 7px;
        transform: translate(50%,-50%);
        content: '';
        background-image: url('data:image/svg+xml,<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 392.4 51.1" style="enable-background:new 0 0 392.4 51.1;" xml:space="preserve"><path fill="%23000" d="M346.8,8.5L366,21.6l-366,0v10.2l363.7,0l-18.1,10.4l5.1,8.9l41.7-24L352.5,0L346.8,8.5z"/></svg>');
        background-repeat: no-repeat;
        background-position: center center;

    }
    &:hover {
        color: #000;
    }

}

p.lead {
    text-transform: uppercase;
    font-weight: 500;
}

.btn-light {
    text-transform: none;
    // padding-left: 4rem;
    // padding-right: 4rem;
    &:hover {
        background: $primary;
        color: #fff;
    }
}

.btn-upload-image {
    font-weight: 400;
    text-transform: none;
    background: #ededed;
    width: 100%;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
}

.avatar-img {
    border-radius:50%;
    overflow: hidden;
}

svg.icon {
    width: 40px;
    height: 40px;
}

svg.icon-long-arrow {
    width: 90px;
    height: 12px;
    color: #000;
}

.btn-arrow {
    svg.icon-long-arrow {
        width: 60px;
        height: 7px;
        margin-left: 2rem;
    }   
}

.img-rounded-right-top {
    border-top-right-radius: 74px;
    overflow: hidden;
}
.img-rounded-left-top {
    border-top-left-radius: 74px;
    overflow: hidden;
}

.btn-blue {
    background: $primary;
    color: #fff;
    &:hover {
        color: #fff;
        background: lighten($primary, 10%);
    }
}

.btn-success {
    color: #fff;
    &:hover {
        color: #fff;
    }
}

.btn-like {
    padding-left: 1rem;
    padding-right: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    .icon {
        width: 32px;
        height: 28px;
    }
}

.btn-invest-count {
    text-transform: none;
    font-weight: 400;
    span {
        font-weight: bold;
        font-size: 21px;
    }
}



.section-title {
    font-size: 40px;
    font-weight: 100;
    margin-bottom: 4rem;
    text-transform: uppercase;
    @include media-breakpoint-down(sm) {
        margin-bottom: 3rem;
    }
    @include media-breakpoint-down(sm) {
        font-size: 30px;
        margin-bottom: 2rem;
    }
    &--small {
        font-size: 24px;
        font-weight: 500;
        @include media-breakpoint-down(sm) {
            font-size: 24px;    
        }

    }
}

.block-media {
    display: flex;
    flex-wrap:wrap;
    // gap:30px;
    align-items: center;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;

    &-text {
        flex:1 0 100%;
        @include media-breakpoint-up(lg) {
            flex: 0 1 50%;
            
        }
    }
    &-image {
        flex:1 0 100%;

        @include media-breakpoint-up(lg) {
            flex: 0 1 50%;
        }
        img {
            max-width:100%;
            height: auto;
        }
    }
    &--reverse {
        .block-media-text {
            @include media-breakpoint-up(lg) {
                order:2;
            }
        }
        .block-media-image {
            @include media-breakpoint-up(lg) {
                order:1;
            }
        }
    }
}

.btn-wide {
    @media(min-width:600px) {
        min-width:300px;
    }
}



.bg-green-right {
    position: relative;
    padding-top: 2rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }


    
    &:before {
        position: absolute;
        top: 0;
        left: 0;
        content: '';
        background: #c6e8cc;
        width: 100%;
        height: 100%;
        border-top-left-radius: 100px;
        z-index: 10;
        width: calc(100% + 15px);
        
        @each $breakpoint, $container-max-width in $container-max-widths {
            @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
              width: calc(100% + ((100vw - #{$container-max-width}) / 2) + 30px);
              margin-right: calc(0px - (100vw - #{$container-max-width}) / 2 - 15px);
            }
          }
    }

    &-content {
        position: relative;
        z-index: 50;
        padding-left: 4rem;
    }
}


.bg-green-left {
    position: relative;
    padding-top: 2rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }


    
    &:before {
        position: absolute;
        top: 0;
        left: 0;
        content: '';
        background: #c6e8cc;
        width: 100%;
        height: 100%;
        border-top-right-radius: 100px;
        z-index: 10;

        margin-left: -15px;

        @each $breakpoint, $container-max-width in $container-max-widths {
            @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
              width: calc(100% + ((100vw - #{$container-max-width}) / 2) + 30px);
              margin-left: calc(0px - (100vw - #{$container-max-width}) / 2 - 30px);
            }
          }
    }

    &-content {
        position: relative;
        z-index: 50;
        padding-right: 4rem;
    }
}

.bg-light-right {
    position: relative;
    padding-top: 4rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }


    
    &:before {
        position: absolute;
        top: 0;
        left: 0;
        content: '';
        background: #fbfbfb;
        width: 100%;
        height: 100%;
        border-top-left-radius: 100px;
        z-index: 10;
        margin-left: -15px;
        @each $breakpoint, $container-max-width in $container-max-widths {
            @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
              width: calc(100% + ((100vw - #{$container-max-width}) / 2) + 15px);
              margin-right: calc(0px - (100vw - #{$container-max-width}) / 2 - 15px);
            }
          }
    }

    &-content {
        position: relative;
        z-index: 50;
    }
}

.pagination {
    margin-top: 4rem;
    margin-bottom: 4rem;
    
    .page-item {
        border: 0;
    }
    .page-link {
        border: 0;
        color: #000;
        svg.icon {
            width: 12px;
            height: 21px;
        }
    }
    .page-item.active {
        .page-link {
            background: transparent;
            color: $primary;
        }
    }
}



.form-check-acceptance {
    font-size: 14px;
    color: #4c4c4c;
}


.countdown {
    display: flex;
    justify-content: space-between;
}


.list-check {
    list-style:none;
    margin-left: 0;
    padding-left: 0;
    li {
        position: relative;
        padding-left: 2.5rem;
        margin-bottom: 1rem;
        &:before {
            background-image: url('data:image/svg+xml,<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 428.8 384" style="enable-background:new 0 0 428.8 384;" xml:space="preserve"><g><path fill="%233e6eb4" d="M180.6,384c-0.1,0-0.1,0-0.2,0c-2.5,0-4.8-1.1-6.5-2.9L2.4,194.6c-2.9-3.2-3.2-8-0.6-11.5c2.6-3.5,7.2-4.7,11.2-2.9l156.9,73.5c1.3,0.6,2.9,0.3,3.9-0.7L413.2,2.8c3.2-3.4,8.5-3.8,12.2-0.8s4.5,8.2,2,12.1L189.2,378.8c-0.3,0.5-0.7,1-1.2,1.5l-1.1,1C185.3,383,183,384,180.6,384L180.6,384z"/></g></svg>');
            position: absolute;
            left: 0;
            top: 0;
            content: '';
            width: 26px;
            height: 23px;
            color: $primary;
            top: 4px;
        }
    }
}


.swiper {
    padding-bottom: 4rem;
    .swiper-pagination {
        bottom: 1rem;
        &-bullet {
            width: 20px;
            height: 20px;
            background: $success;
        }
    }
}


