.invest-steps {
    display: flex;
    @include make-row();
    .invest-step {
        color: #000;
        @include make-col-ready();
            @include make-col(12);
            padding-top: 1rem;
            padding-bottom: 1rem;
            @include media-breakpoint-up(lg) {
                padding-top: 0;
                padding-bottom: 0;
            @include make-col(3);
        }
        display: flex;

    }
    .invest-step-inner {
        display: flex;
        flex-direction: column;
        width: 100%;
        
        background: #fbfbfb;
        border-radius: 4rem;
        position: relative;
        padding: 2rem 2rem;
        position: relative;
        z-index: 10;
        @include transition($transition-base);
        &:hover {
            background: #fff;
            box-shadow:0 0 32px rgba(0,0,0,0.2);
        }
        h2 {
            font-size: 30px;
            font-family: 'Museo Sans';
            margin-bottom: 4rem;
            padding-left: 2rem;
            line-height: 1.2;
            min-height:2.4em;
        }
    }
    .step-top, .step-middle, .step-bottom {
        position: relative;
        z-index: 50;
    }
    .step-bottom {
        margin-top: auto;
        padding-top: 1rem;
        text-align: right;
    }
    .icon-long-arrow {
        width: 60px;
        height: 7px;
    }
    .number {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 20;
        font-size: 200px;
        line-height: 1;
        font-weight: 800;
        color: #c9ddff;
        transform: translateY(-30%);
    }
}