.section-counters {
    padding-top: 2rem;
    padding-bottom: 2rem;
    @include media-breakpoint-up(lg) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
}

.counters-col {
    position: relative;
    + .counters-col {
        @include media-breakpoint-up(lg) {
            &:before {
                position: absolute;
                left: 0;
                top: 50%;
                width: 1px;
                height: 90px;
                background: #cbcbcb;
                content: '';
                transform:translateY(-50%);
            }
        }
    }
}


.counters-item {
    text-align: center;
    
    .icon {
        text-align: center;
        margin-bottom: 2rem;
        &-img {
            height: 60px;
        }
    }
    .counter {
        color: $primary;
        font-weight: bold;
        font-size: 30px;
    }
    p {
        font-size: 18px;
        margin-bottom: 0;
        @include media-breakpoint-up(lg) {
            padding-left: 10%;
            padding-right: 10%;
        }
    }
}