.pagination {
    
    .page-item {
        display: flex;
        // background: #caddff;
        &.active {
            .page-link {
                background: #caddff;
            }
        }
        .page-link {
            background: #caddff;
            padding-top: 1rem;
        position: relative;
        padding-bottom: 1rem;

        }
        &.prev, &.next {
            .icon {
                width: 60px;
                height: 7px;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
            }
        }
        &.prev {
            .page-link {
                padding-right: 2rem;
                border-top-left-radius: 30px;
                border-bottom-left-radius: 30px;
            }
            .icon {
                right: .75rem;
            }
        }
        &.next {
            .page-link {
                padding-left: 2rem;
                border-bottom-right-radius: 30px;
            }
            .icon {
                left: .75rem;
            }
        }
    }

}